#markdown-toc {
    display: none;
}
.page[post] {
    .label {
        margin-top: 16px;
        overflow: hidden;
        .label-card {
            float: left;
            margin-right: 15px;
            color: #ceceb6;
            .point {
                padding: 0 10px;
                color: #9d9d9d;
            }
            i {
                padding-right: 5px;
                color: #6b6a6a;
            }
        }
    }
    a {
        color: #ceceb6;
    }
    b {
        color: #ceceb6;
        background-color: #0d866a;
    }
    .left {
        color: #ceceb6;
        ul {
            >h2 {
                color: #333;
            }
            li {
                margin: 0;
            }
        }
    }
}
@media screen and (max-width: 770px) {
    .page[post] {
        .left {
            font-size: 15px;
            h2 {
                color: #4ac8ff;
                font-size: 24px;
            }
            h3 {
                color: #4ac8ff;
                font-size: 20px;
            }
            h4 {
                color: #4ac8ff;
                font-size: 18px;
            }
            h5 {
                color: #4ac8ff;
                font-size: 16px;
            }
            h6 {
                color: #4ac8ff;
                font-size: 15px;
            }
        }
    }
}
