.clearfix:after {
    content: "\200B";
    display: block;
    height: 0;
    clear: both;
}
.clearfix {
    *zoom: 1;
}

@mixin right {
    scrollbar-width: 10px;
    float: right;
    width: 285px;
    padding-left: 20px;
    top: 0;
    .fixed {
        position: fixed;
        top: 20px;
    }
    .scroll-bottom {
        position: absolute;
        bottom: 190px;
    }
    .side {
        box-sizing: border-box;
        background-color: #f8f8fd;
        box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.3), 0 0 30px rgba(10, 10, 0, 0.1) inset;
        padding-bottom: 1px;
        -ms-word-break: break-all;
        word-break: break-all;
        /* Non standard for webkit */
        // word-break: break-word;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        -ms-hyphens: auto;
        hyphens: auto;
        >div {
            padding: 12px 15px;

            // margin-right: -10px;
            border-bottom: 1px solid #c0c0c0;
            font-size: 16px;
            font-weight: bold;
        }
        a {
            color: #4d6dad;
            &:hover {
                color: #236c8c;
            }
        }
    }
    .content-ul {
        overflow-y: auto;
        padding-right: 10px;
        line-height: 1.8;
        padding-left: 35px;
        ul {
            padding-left: 20px;
        }
    }
}

@mixin anchor {
    display: none;
}

.home-right-bar {
    position: fixed;	        /* 设置fixed固定定位 */
    top: 75px;
    bottom: auto;		/* 距离浏览器窗口下边框20px */
    right: 20px;		/* 距离浏览器窗口右边框20px */
    .anchor { @include anchor}
    .right{ @include right}
}

.post-right-bar {
    position: fixed;
    box-sizing: border-box;
    margin: 0 auto;
    .anchor { @include anchor}
    .right{
        @include right;
    }
}

.modal {
    display: none;
    position: fixed;
    z-index: 999;
    /*padding-top: 100px;*/
    left: 0;
    top: 0;
    width: 100% !important;
    height: 100% !important;
    overflow: auto;
    background-color: rgba(23, 23, 23, 0.9);
}

.modal-content {
    margin: auto;
    display: block;
    width: 75%;
}

/* 移动设备样式 */
@media (max-width: 768px) {
    .modal-content {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 95%;
        height: 95%;
    }
}

.modal-content img {
    width: 100%;
    height: auto;
}
.modal-content .prev-div{
    padding: 30px;
    position: absolute;
    top: 50%;
    left: 5%;
}

.modal-content .next-div{
    padding: 30px;
    position: absolute;
    top: 50%;
    right: 5%;
}
.btn-s {
    border: none;
}

.modal-content .prev-btn {

    width: 2rem;
    height: 2rem;
    transform: rotate(45deg);
    border-left: 1px solid #fff0ff;
    border-bottom: 1px solid #fff0ff;
    background-color: transparent;
    cursor: pointer;
    outline: none; /* 禁用默认焦点样式 */
    transition: background-color 0.3s;

    touch-action: manipulation; /* 控制触摸行为 */
    -webkit-tap-highlight-color: transparent; /* 禁用移动设备上的点击高亮效果 */
}


.modal-content .next-btn {
    width: 2rem;
    height: 2rem;

    transform: rotate(-45deg);
    background-color: transparent;
    border-right: 1px solid #fff0ff;
    border-bottom: 1px solid #fff0ff;
    cursor: pointer;
    outline: none; /* 禁用默认焦点样式 */
    transition: background-color 0.3s;

    touch-action: manipulation; /* 控制触摸行为 */
    -webkit-tap-highlight-color: transparent; /* 禁用移动设备上的点击高亮效果 */
}

.prev-btn:hover {
    border-color: #ddd;
}

.next-btn:hover {
    border-color: #ddd;
}

/* 移动设备样式 */
@media (max-width: 768px) {
    .modal-content .prev-div {
        top: 80%;
        left: 10%;
    }

    .modal-content .next-div {
        top: 80%;
        right: 10%;
    }
}

.page {
    width: 1140px;
    box-sizing: border-box;
    margin: 0 auto;
    // word-break: break-all;
    .left {
        padding: 20px 30px;
        line-height: 1.6;
        font-size: 20px;
        background-color: #222;
        box-shadow: 0 1px 2px rgba(112, 112, 112, 0.4),0 0 30px rgba(10,10,0,0.1) inset;
        >h1 {
            margin-bottom: -10px;
            color: #4ac8ff;
        }
        .small {
            color: #898983;
        }
        .like-post {
            margin: 10px 0 10px 0;
            text-align: center;
        }
        a:not([title]){
            word-break: break-all;
        }
        ul {
            >h2 {
                margin-left: -40px;
                color: #4ac8ff;
            }
            li {
                >h2 {
                    margin-left: -40px;
                    color: #4ac8ff;
                }
                line-height: 1.8;
                color: #ceceb6;
                time {
                    display: inline-block;
                    width: 135px;
                }
                .title {
                    font-weight: bold;
                    color: #4ac8ff;
                    transition: 0.3s ease;
                    &:hover {
                        color: #4d6dad;
                    }
                }
                i {
                    margin-right: 5px;
                }
                .categories {
                    padding: 0 15px;
                    i {
                        color: #9e9d9d;
                    }
                    a {
                        padding: 0 5px;
                        background-color: #0d866a;
                        color: #fff;
                        font-size: 90%;
                        transition: 0.3s ease;
                        &:hover {
                            background-color: #b6b6b9;
                        }
                    }
                }
                .pageTag {
                    i {
                        color: #c6cbe9;
                    }
                    a {

                        // border: 1px solid #aaa;
                        padding: 0 5px;
                        background-color: #0d866a;
                        border-radius: 4px;
                        color: #fff;
                        font-size: 90%;
                        transition: 0.3s ease;
                        &:hover {
                            background-color: #c0ddc7;
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width:1200px) and (min-width: 770px) {
    .page {
        width: 90%;
    }
}
@media screen and (max-width: 770px) {
    .page {
        width: 95%;
        .left {
            width: 100%;
            float: left;
            padding: 10px 14px;
            font-size: 14px;
            >h1 {
                font-size: 25px;
                margin-bottom: -10px;
            }
            ul {
                padding-left: 20px;
                >h2 {
                    font-size: 20px;

                    // margin: 0;
                    margin-left: -20px;
                }
                li {
                    margin: 20px 0;
                    time {
                        display: block;
                        width: auto;
                    }
                    .title {
                        display: block;
                        font-size: 16px;
                    }
                    .categories {
                        font-size: 12px;
                        padding-left: 0;
                        padding-right: 10px;
                    }
                }
            }
        }
        .anchor {
            display: block;
            visibility: visible;
            position: fixed;
            top: 76px;
            right: 2.5%;
            padding: 5px 10px;
            color: #333;
            font-size: 15px;
            background-color: #f8f8fd;
            box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.3), 0 0 30px rgba(10, 10, 0, 0.1) inset;
            opacity: 0.7;
            transition: 0.2s cubic-bezier(0.5, 0.1, 0.51, 1.38);
        }
        .anchor-hide {
            opacity: 0;
            visibility: hidden;
        }
        .right-hide {
            visibility: hidden;

            // display: none;
            position: fixed;

            // z-index: 190;
            float: none;
            width: auto;
            max-width: 95%;
            padding: 0;
            top: 76px;
            right: 2.5%;
            opacity: 0;
            transform-origin: 100% 0;
            transform: scale(0.1);
            transition: 0.2s cubic-bezier(0.5, 0.1, 0.51, 1.38);
        }
        .right-show {
            z-index: 103;
            visibility: visible;
            opacity: 1;
            transform: scale(1);
            .content-ul{
              margin-bottom: 0;
              >li:last-of-type{
                margin-bottom: 16px;
              }
            }
        }
    }
}

