body,
html,
div,
header,
footer,
nav,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 400;
}
html {
    height: 100%;
}
body {
    font-family: 'Helvetica Neue',Helvetica,Arial,'Microsoft YaHei',sans-serif;
    color: #333;
    position: relative;
    min-height: 100%;
    padding-bottom: 190px;
    background-color: #236c8c;
    background-image: url("https://damonc-top2.github.io/web-assets/commons/BlogBackgroundSLS-3.webp");
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
a {
    text-decoration: none;
}
button {
    border: none;
    outline: none;
}
hr {
    border: 0;
    border-top: 1px solid #eee;
    margin: 20px 0;
}

///////////
h1 {
    font-size: 30px;
}
h2 {
    font-size: 26px;
    border-bottom: 1px solid #eee;
    margin: 50px 0 25px;
    color: #4ac8ff;
}
h3 {
    font-size: 23px;
    margin: 50px 0 20px;
    color: #4ac8ff;
}
h4 {
    font-size: 19px;
    margin: 30px 0 10px;
    color: #4ac8ff;
}
h5 {
    font-size: 17px;
    margin: 20px 0 10px;
    color: #4ac8ff;
}
h6 {
    font-size: 16px;
    margin: 10px 0 5px;
    color: #4ac8ff;
}
blockquote {
    color: rgb(179, 179, 179);
    border-left: 5px solid #4ac8ff;
    padding-left: 15px;
    background: rgba(23, 23, 23, 0.55);
    padding: 10px 20px;
    margin: 0 0 20px;
    p:last-of-type{
        margin: 0;
    }
}
table {
    margin: 0 auto 0 auto;
    thead {
        border-bottom: 1px solid #777;
        background-color: #00000087;
        color: #fff;
    }
    th {
        padding: 2px 10px;
        text-align: left;
    }
    tbody {
        tr {
            &:nth-of-type(2n+1) {
                background-color: #444;
            }
            &:nth-of-type(2n) {
                background-color: #5c5c5c;
            }
        }
    }
    td {
        padding: 2px 10px;
    }
}
p {
    margin: 0 0 14px;
}
img {
    max-width: 100%;
}
